import React, { Component } from "react";
import "./Styles/DailyDetails.css";
import { introPageTransition } from "./dependencies";
import { S } from "selector_dom";
import { userData, data } from "./Data";

export default class DailyDetails extends Component {
  componentDidMount() {
    window.location.pathname === "/summary"
      ? S("#dailyDetailsHandlePageHeader").css({ display: "none" })
      : introPageTransition("dailyDetailsHandlePage");
  }

  dailyDetailsNutritionSum = (n) => {
    const getDataByDate = userData.diary.filter((e) => e.date === this.props.date)[0];
    let sum = 0;
    if (getDataByDate) {
      for (let i in getDataByDate) {
        if (i !== "date" && i !== "eaten") {
          const getDataById = getDataByDate[i].map(
            (e) => (e = Object.assign({}, data.filter((el) => el.id === e[0])[0], { value: e[1], input: e[2] }))
          );

          sum += getDataById.map((e) => e[n] * ((e.value * e.input) / 100)).reduce((a, b) => a + b);
        }
      }
    }
    return sum;
  };

  remainKcalSum = () => Math.round(userData.userInfo.dailykcal - this.dailyDetailsNutritionSum("kcal"));
  eatenKcalPercent = () => Math.round((Math.round(this.dailyDetailsNutritionSum("kcal")) / userData.userInfo.dailykcal) * 100);

  allCarbSum = () => Math.round(userData.userInfo.dailyCarbs - this.dailyDetailsNutritionSum("carb"));
  carbPercent = () => Math.round((Math.round(this.dailyDetailsNutritionSum("carb")) / userData.userInfo.dailyCarbs) * 100);

  allProteinSum = () => Math.round(userData.userInfo.dailyProtein - this.dailyDetailsNutritionSum("protein"));
  proteinPercent = () =>
    Math.round((Math.round(this.dailyDetailsNutritionSum("protein")) / userData.userInfo.dailyProtein) * 100);

  allFatSum = () => Math.round(userData.userInfo.dailyFat - this.dailyDetailsNutritionSum("fat"));
  fatPercent = () => Math.round((Math.round(this.dailyDetailsNutritionSum("fat")) / userData.userInfo.dailyFat) * 100);

  backButtonHandle = () => window.history.back();

  render() {
    return (
      <div id="dailyDetailsHandlePage">
        <header id="dailyDetailsHandlePageHeader">
          <svg id="dailyDetailsBack" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" onClick={this.backButtonHandle}>
            <path d="M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z" />
          </svg>
          <p id="dailyDetailsHeaderTitle">تـفـاصـيـل الـيـوم</p>
        </header>
        <div style={{ backgroundColor: "var(--background-white" }}>
          <p id="intakeTitle">{"( " + this.props.date + " )  المستهلكة في يوم "}</p>
          <div className="intakePercentTitlesContainer">
            <p style={{ color: this.remainKcalSum() < 0 ? "#FE5F8F" : "var(--text-kinda-black)" }}>السعرات</p>
            <p style={{ color: this.remainKcalSum() < 0 ? "#FE5F8F" : "var(--text-kinda-black)" }}>
              {(this.remainKcalSum() < 0 ? this.remainKcalSum() * -1 + " زائــدة" : this.remainKcalSum() + " مـتـبـقـيـة") +
                " / " +
                userData.userInfo.dailykcal}
            </p>
          </div>
          <div className="intakePercentContainer">
            <div
              style={{
                width: this.eatenKcalPercent() + "%",
                backgroundImage:
                  this.remainKcalSum() < 0
                    ? "linear-gradient(-225deg, #FE5F8F 0%, #FE5F8F 100%)"
                    : "linear-gradient(-225deg, var(--main-color) 0%, var(--second-color) 100%)",
              }}
            ></div>
          </div>
          <div className="intakePercentTitlesContainer">
            <p style={{ color: this.allCarbSum() < 0 ? "#FE5F8F" : "var(--text-kinda-black)" }}>الكربوهيدرات</p>
            <p style={{ color: this.allCarbSum() < 0 ? "#FE5F8F" : "var(--text-kinda-black)" }}>
              {(this.allCarbSum() < 0 ? this.allCarbSum() * -1 + " غ زائــدة" : this.allCarbSum() + " غ مـتـبـقـيـة") +
                " / " +
                userData.userInfo.dailyCarbs +
                " غ"}
            </p>
          </div>
          <div className="intakePercentContainer">
            <div
              style={{
                width: this.carbPercent() + "%",
                backgroundImage:
                  this.allCarbSum() < 0
                    ? "linear-gradient(-225deg, #FE5F8F 0%, #FE5F8F 100%)"
                    : "linear-gradient(-225deg, var(--main-color) 0%, var(--second-color) 100%)",
              }}
            ></div>
          </div>
          <div className="intakePercentTitlesContainer">
            <p style={{ color: this.allProteinSum() < 0 ? "#FE5F8F" : "var(--text-kinda-black)" }}>البروتين</p>
            <p style={{ color: this.allProteinSum() < 0 ? "#FE5F8F" : "var(--text-kinda-black)" }}>
              {(this.allProteinSum() < 0 ? this.allProteinSum() * -1 + " غ زائــدة" : this.allProteinSum() + " غ مـتـبـقـيـة") +
                " / " +
                userData.userInfo.dailyProtein +
                " غ"}
            </p>
          </div>
          <div className="intakePercentContainer">
            <div
              style={{
                width: this.proteinPercent() + "%",
                backgroundImage:
                  this.allProteinSum() < 0
                    ? "linear-gradient(-225deg, #FE5F8F 0%, #FE5F8F 100%)"
                    : "linear-gradient(-225deg, var(--main-color) 0%, var(--second-color) 100%)",
              }}
            ></div>
          </div>
          <div className="intakePercentTitlesContainer">
            <p style={{ color: this.allFatSum() < 0 ? "#FE5F8F" : "var(--text-kinda-black)" }}>الدهون</p>
            <p style={{ color: this.allFatSum() < 0 ? "#FE5F8F" : "var(--text-kinda-black)" }}>
              {(this.allFatSum() < 0 ? this.allFatSum() * -1 + " غ زائــدة" : this.allFatSum() + " غ مـتـبـقـيـة") +
                " / " +
                userData.userInfo.dailyFat +
                " غ"}
            </p>
          </div>
          <div className="intakePercentContainer">
            <div
              style={{
                width: this.fatPercent() + "%",
                backgroundImage:
                  this.allFatSum() < 0
                    ? "linear-gradient(-225deg, #FE5F8F 0%, #FE5F8F 100%)"
                    : "linear-gradient(-225deg, var(--main-color) 0%, var(--second-color) 100%)",
              }}
            ></div>
          </div>
        </div>
        <p id="infoTitle">التفاصيل الغذائية</p>
        <div id="dailyDetailsNutritionInfoDetail">
          <div id="dailyDetailsNutritionInfoTitles">
            <p className="dailyDetailsNutritionInfoMain">البروتين</p>
            <p className="dailyDetailsNutritionInfoMain">الكربوهيدرات</p>
            <p className="dailyDetailsNutritionInfoSub">الألياف</p>
            <p className="dailyDetailsNutritionInfoSub">السكر</p>
            <p className="dailyDetailsNutritionInfoMain">الدهون</p>
            <p className="dailyDetailsNutritionInfoSub">الدهون المشبعة</p>
            <p className="dailyDetailsNutritionInfoSub">الدهون الغير مشبعة</p>
            <p className="dailyDetailsNutritionInfoMain">آخر</p>
            <p className="dailyDetailsNutritionInfoSub">الكوليسترول</p>
            <p className="dailyDetailsNutritionInfoSub">الصوديوم</p>
            <p className="dailyDetailsNutritionInfoSub">البوتاسيوم</p>
          </div>
          <div id="dailyDetailsNutritionInfoValues">
            <p className="dailyDetailsNutritionInfoMain">{Number(this.dailyDetailsNutritionSum("protein").toFixed(1))} غ</p>
            <p className="dailyDetailsNutritionInfoMain">{Number(this.dailyDetailsNutritionSum("carb").toFixed(1))} غ</p>
            <p className="dailyDetailsNutritionInfoSub">{Number(this.dailyDetailsNutritionSum("fiber").toFixed(1))} غ</p>
            <p className="dailyDetailsNutritionInfoSub">{Number(this.dailyDetailsNutritionSum("sugar").toFixed(1))} غ</p>
            <p className="dailyDetailsNutritionInfoMain">{Number(this.dailyDetailsNutritionSum("fat").toFixed(1))} غ</p>
            <p className="dailyDetailsNutritionInfoSub">{Number(this.dailyDetailsNutritionSum("saturatedFat").toFixed(2))} غ</p>
            <p className="dailyDetailsNutritionInfoSub">{Number(this.dailyDetailsNutritionSum("unsaturatedFat").toFixed(2))} غ</p>
            <p className="dailyDetailsNutritionInfoMain" style={{ color: "rgb(0 ,0 ,0 ,0)" }}>
              ......
            </p>
            <p className="dailyDetailsNutritionInfoSub">{this.dailyDetailsNutritionSum("cholesterol").toFixed(0)} ملغ</p>
            <p className="dailyDetailsNutritionInfoSub">{this.dailyDetailsNutritionSum("sodium").toFixed(0)} ملغ</p>
            <p className="dailyDetailsNutritionInfoSub">{this.dailyDetailsNutritionSum("potassium").toFixed(0)} ملغ</p>
          </div>
        </div>
      </div>
    );
  }
}
